<template>

  <div>
    <h3>订单信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="24" :lg="24">

        <Form :model="formData" :label-width="120">

          <FormItem style="display: inline-block;width: 51%;margin-bottom: 0" label="选择团队:">
            <Select v-model="formData.team_id">
              <Option v-for="(item,index) in teams" v-bind:key="index" :label="item.company"
                      :value="item.id"></Option>
            </Select>
            如需创建新的团队，您可<a> 前往团队维护创建></a>
          </FormItem>

          <Divider></Divider>

          <div v-for="(item,index) in workplaces" v-bind:key="index" style="margin-top: 30px">

            <FormItem style="display: inline-block;width: 25%" label="选择空间:">
              <Select @on-change="selectPlace(item)" v-model="item.space_id">
                <Option v-for="(space,index) in item.spaces" v-bind:key="index" :label="space.name"
                        :value="space.id"></Option>
              </Select>
            </FormItem>

            <FormItem v-show="item.type===0" style="display: inline-block;width: 25%" label="选择工位:">
              <Select @on-change="selectIdWork(item)" multiple v-model="item.workplaceIds" :loading="loadSpaceIng"
                      loading-text="加载中">
                <Option v-for="(workplace,index) in item.workplaces" :disabled="itemDisable(workplace.id)"
                        v-bind:key="index"
                        :label="workplace.workplace_no"
                        :value="workplace.id"></Option>
              </Select>
            </FormItem>

            <FormItem v-show="item.type===1" style="display: inline-block;width: 25%" label="选择工位:">
              <Select @on-change="selectAreaWork(item)" v-model="item.workplaceArea" :loading="loadSpaceIng"
                      loading-text="加载中">
                <Option v-for="(workplace,index) in item.workplaces" v-bind:key="index"
                        :disabled="itemDisable(workplace.id)" :label="workplace.workplace_no"
                        :value="index"></Option>
              </Select>
            </FormItem>

            <RadioGroup @on-change="selectPlace(item)" style="width: 15%;margin-left: 1%" v-model="item.type">
              <Radio :label="0">
                <span>按工位</span>
              </Radio>
              <Radio :label="1">
                <span>按面积</span>
              </Radio>
            </RadioGroup>

            <FormItem v-if="item.areaSelect" style="display: inline-block;width: 20%" label="输入面积:">
              <InputNumber @on-change="setPrice" :max="item.areaSelect.max_area" :min="1"
                           v-model="item.areaSelect.area"></InputNumber>
            </FormItem>

            <a v-if="index === 0" @click="add()" style="margin-left: 10px;line-height: 35px"> 添加新的工位></a>
            <a v-if="index !== 0" @click="del(index)" style="margin-left: 10px;line-height: 35px"> 删除></a>

          </div>

          <Divider></Divider>
          <div>
            <FormItem style="display: inline-block;width: 51%;" label="合同模板:">
              <Select
                @on-change="selectTemplate"
                v-model="selectTemplateIndex">
                <Option v-for="(item,index) in templates" v-bind:key="index" :label="item.name"
                        :value="index"></Option>
              </Select>
            </FormItem>

            <div v-if="formData.custom_content!=null" style="margin-top: -30px">
              <Divider orientation="left"> 自定义变量</Divider>
              <FormItem v-for="(item,index) in formData.custom_content" v-bind:key="index" :label="item.name"
                        style="width: 50%">
                <Input v-model="item.placeholder" placeholder="请填写变量"></Input>
              </FormItem>
            </div>
            <Divider></Divider>

            <FormItem style="display: inline-block;width: 51%;" label="合同年限:">
              <InputNumber :min="1" v-model="formData.contract_years" style="width: 100%"></InputNumber>
            </FormItem>

            <FormItem style="display: inline-block;width: 51%;" label="起租时间:">
              <DatePicker type="date" placeholder="选择时间" v-model="formData.startTime" style="width: 100%"></DatePicker>
            </FormItem>

            <FormItem style="display: inline-block;width: 51%;" label="付款方式:">
              <Select v-model="formData.pay_type">
                <Option :value="1" label="月付"></Option>
                <Option :value="2" label="季付"></Option>
                <Option :value="3" label="半年付"></Option>
                <Option :value="4" label="年付"></Option>
                <Option :value="5" label="金额"></Option>
              </Select>
            </FormItem>

            <FormItem style="display: inline-block;width: 51%;" label="定金:">
              <InputNumber @on-change="setPrice" :min="0" v-model="formData.deposit" style="width: 100%"></InputNumber>
            </FormItem>

          </div>

          <Divider></Divider>

          <div>

            <FormItem label="开具类型">
              <RadioGroup @on-change="selectOpenerType()" v-model="formData.opener_type">
                <Radio :label="1"><span>个人</span></Radio>
                <Radio :label="2"><span>企业</span></Radio>
              </RadioGroup>
            </FormItem>

            <div v-if="formData.opener_type===1">
              <FormItem label="发票抬头">个人</FormItem>
              <FormItem label="发票类型">增值税普通发票</FormItem>
            </div>

            <div v-if="formData.opener_type===2">

              <FormItem label="发票抬头" style="width: 51%">
                <Input v-model="formData.invoice_title" placeholder="请填写发票抬头"></Input>
              </FormItem>

              <FormItem label="发票类型">

                <RadioGroup v-model="formData.invoice_type">
                  <Radio :label="1">企业增值税普通发票</Radio>
                  <Radio :label="2">增值税专用发票</Radio>
                  <Radio :label="3">组织增值税普通发票</Radio>
                </RadioGroup>

              </FormItem>

              <FormItem label="税务登记证号" style="width: 51%">
                <Input v-model="formData.tax" placeholder="请填写税务登记证号"></Input>
              </FormItem>

              <FormItem label="基本开户银行名称" style="width: 51%">
                <Input v-model="formData.bank" placeholder="请填写基本开户银行名称"></Input>
              </FormItem>

              <FormItem label="基本开户账号" style="width: 51%">
                <Input v-model="formData.account" placeholder="请填写基本开户账号"></Input>
              </FormItem>

              <FormItem label="注册场所地址" style="width: 51%">
                <Input v-model="formData.address" placeholder="请填写注册场所地址"></Input>
              </FormItem>

              <FormItem label="注册固定电话" style="width: 51%">
                <Input v-model="formData.phone" placeholder="请填写注册固定电话"></Input>
              </FormItem>
            </div>

          </div>

          <Divider></Divider>

          <div>

            <FormItem label="优惠金额" style="width: 51%">
              <InputNumber :min="0" v-model="formData.discount" style="width: 100%"></InputNumber>
            </FormItem>

            <FormItem label="备注" style="width: 51%">
              <Input v-model="formData.remark" type="textarea" placeholder="请填写备注"></Input>
            </FormItem>

          </div>

          <Divider></Divider>
          <FormItem label="抄送人" style="width: 51%">
            <Select multiple v-model="formData.copy_ids"
            >
              <Option v-for="(item,index) in copies"
                      v-bind:key="index"
                      :label="item.nickname"
                      :value="item.id"></Option>
            </Select>
          </FormItem>
          <Divider></Divider>

          <FormItem label="订单总价" style="width: 100%">
            <h1 style="display: inline-block">￥{{ formData.total_price }}</h1>
            <h3 style="margin-left: 20px;display: inline-block">优惠金额￥{{ formData.discount }}</h3>
            <Button @click="submit()" style="margin-left: 20px" type="success">保存</Button>
          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'
import teamApi from '../team/api'
import spaceApi from '../space/api'
import workplaceApi from '../workplace/api'
import templateApi from '../contract/template/api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
        opener_type: 2,
        discount: 0,
        total_price: 0,
        deposit: 0,
        contract_years: 1
      },
      load: false,
      spaces: [],
      templates: [],
      teams: [],
      workplaces: [],
      loadSpaceIng: false,
      selectIds: [],
      selectTemplateIndex: null,
      copies: []
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
    this.loadTeam()
    this.loadSpace()
    this.loadTemplates()
    this.loadCopy()
  },
  methods: {
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {}, () => {})
    },
    selectOpenerType () {
      if (this.formData.opener_type === 1) {
        this.formData.invoice_title = '个人'
        this.formData.invoice_type = 2
      } else {
        this.formData.invoice_title = null
        this.formData.invoice_type = null
      }
    },
    selectTemplate () {
      console.log(this.selectTemplateIndex)
      let data = this.templates[this.selectTemplateIndex]
      this.formData.contract_template_no = data.template_no
      this.formData.custom_content = data.content
    },
    loadCopy () {
      api.copy(res => {
        this.copies = res.data.data
      })
    },
    loadTemplates () {
      templateApi.select((res) => {
        this.templates = res.data.data
      })
    },
    loadTeam () {
      teamApi.select(res => {
        this.teams = res.data.data
      })
    },
    loadSpace () {
      spaceApi.select(res => {
        this.spaces = res.data.data
        this.workplaces.push({ spaces: this.spaces, type: 0 })
      })
    },
    selectIdWork (item) {
      this.setIds()
    },
    selectAreaWork (item) {
      if (item.type === 0) return
      item.areaSelect = item.workplaces[item.workplaceArea]
      item.areaSelect.area = 1
      this.$set(item, item)
      console.log(item)
      this.setIds()
    },
    itemDisable (id) {
      for (let i = 0; i < this.selectIds.length; i++) {
        if (id === this.selectIds[i]) return true
      }
      return false
    },
    setIds () {
      this.setPrice()
      let ids = []
      this.workplaces.forEach(value => {
        if (value.type === 0 && value.workplaceIds != null) {
          ids = ids.concat(value.workplaceIds)
        } else if (value.type === 1 && value.areaSelect != null) {
          ids.push(value.areaSelect.id)
        }
      })
      this.selectIds = ids
    },
    setPrice () {
      let price = 0
      this.workplaces.forEach(value => {
        if (value.type === 0 && value.workplaceIds != null) {
          value.workplaceIds.forEach(id => {
            value.workplaces.forEach(place => {
              if (place.id === id) {
                price += this.$formUtil.isStrValid(place.price) ? parseFloat(place.price) : 0
              }
            })
          })
        } else if (value.type === 1 && value.areaSelect != null) {
          price += (this.$formUtil.isStrValid(value.areaSelect.price) ? parseFloat(value.areaSelect.price) : 0) * (this.$formUtil.isStrValid(value.areaSelect.area) ? parseFloat(value.areaSelect.area) : 0)
        }
      })
      price = price + (this.$formUtil.isStrValid(this.formData.deposit) ? parseFloat(this.formData.deposit) : 0)
      this.formData.total_price = price
    },
    selectPlace (item) {
      if (item.space_id == null) return
      this.$set(item, 'workplaceIds', null)
      this.$set(item, 'workplaceArea', null)
      this.$set(item, 'areaSelect', null)
      workplaceApi.select({ space_id: item.space_id, type: item.type }, res => {
        this.$set(item, 'workplaces', res.data.data)
      })
      this.setIds()
    },
    del (index) {
      this.workplaces.splice(index, 1)
      this.setIds()
    },
    add () {
      let data = this.workplaces[this.workplaces.length - 1]
      if ((data.type === 0 && data.workplaceIds == null) || (data.type === 1 && data.areaSelect == null)) {
        this.$Message.error('请将上一条填写完整')
        return
      }
      this.workplaces.push({ spaces: this.spaces, type: 0 })
    },
    submit () {
      let data = this.$formUtil.deepCopy(this.formData)
      if (!this.$formUtil.isStrValid(data.team_id)) {
        this.$Message.error('请选择入住团队')
        return
      }
      if (!this.$formUtil.isStrValid(this.selectIds)) {
        this.$Message.error('请至少选择一个工位')
        return
      } else {
        let workplace = []
        this.workplaces.forEach(value => {
          if (value.type === 0 && value.workplaceIds != null) {
            value.workplaceIds.forEach(id => {
              workplace.push({ workplace_id: id })
            })
          } else if (value.type === 1 && value.areaSelect != null) {
            workplace.push({ workplace_id: value.areaSelect.id, workplace_area: value.areaSelect.area })
          }
        })
        data.workplaces = workplace
      }

      if (data.custom_content != null) {
        data.custom_content = JSON.stringify(data.custom_content)
      }
      this.load = true
      api.save(data, () => {
        this.load = false
        this.$router.push({ name: 'contract_template' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

<style scoped>

</style>
